(() => {

  const els = {
    open: document.getElementById('more-info-open'),
    $features: $('.features'),
    close: document.getElementById('more-info-close'),
    $data: $('[data-more-info]'),
  };

  els.$data.toggle();

  $( els.open ).click(() => {
    els.$features.toggleClass('features--state--open');
    els.$data.toggle();
    $( els.open ).toggle();
  });

  $( els.close ).click(() => {
    els.$features.toggleClass('features--state--open');
    els.$data.toggle();
    $( els.open ).toggle();
  });

})();
